.projectDetailsPage {
  display: flex;
  flex-direction: column;
  margin: 0 auto 60px;
  max-width: 1440px;
  overflow: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  padding: 30px 20px;
  scroll-behavior: smooth;
  scrollbar-width: none; /* Firefox */
  width: 90vw;
  &::-webkit-scrollbar {
    height: 0;
    width: 0;
  }

  .projectHero {
    display: flex;
    min-height: 400px;
    overflow: hidden;

    > .projectTitle {
      background-color: #252e3c;
      color: white;
      display: flex;
      flex-basis: 50%;
      flex-direction: column;
      flex-grow: 0;
      justify-content: space-evenly;
      margin-top: -20px;
      padding: 50px;

      span {
        -webkit-box-orient: vertical;
        display: -webkit-box;
        font-size: 40px;
        font-weight: 700;
        -webkit-line-clamp: 2;
        line-height: 65px;
        overflow-y: hidden;
        text-align: left;
        text-overflow: ellipsis;
      }

      .seeProject {
        background: #f7f7f7;
        border-radius: 6px;
        color: black;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: 0.06em;
        line-height: 19px;
        padding: 20px 70px;
        position: relative;
        text-align: left;
        width: max-content;
      }
    }

    > .projectImg {
      flex-basis: 50%;
      > img {
        height: 100%;
        object-fit: contain;
        width: 100%;
      }
    }
  }

  .projectActions {
    padding: 10px 50px;
    display: flex;
    gap: 10px;
  }

  .projectBody {
    display: flex;
    gap: 20px;

    .projectToc {
      display: flex;
      flex-basis: 20%;
      flex-direction: column;
      height: 100vh;
      padding-left: 50px;
      padding-top: 45px;
      position: sticky;
      top: 0;

      a {
        font-size: 20px;
        letter-spacing: 0.06em;
        line-height: 28px;
        margin-bottom: 10px;
        text-align: left;
      }

      .bolden {
        font-weight: 700;
      }
    }

    .projectContent {
      display: flex;
      flex-basis: 80%;
      flex-direction: column;
      padding-top: 45px;
    }

    .projectDetailsTitle {
      font-size: 20px;
      font-weight: 700;
      text-align: left;
    }

    .projectDetailsContent {
      font-size: 20px;
      font-weight: 400;
      padding-right: 40px;
      text-align: justify;
    }

    .separater {
      border: 1px solid #00000040;
      margin: 20px 0;
    }

    .otherDetails {
      margin-bottom: 20px;
      text-align: left;

      .otherDetailsLabel {
        font-size: 20px;
        font-weight: 500;
        margin-right: 15px;
      }

      .otherDetailsContent {
        font-size: 20px;
        font-style: italic;
      }
    }
  }
}

@media only screen and (max-width: 930px) {
  .projectDetailsPage .projectHero .projectTitle {
    padding: 100px;
  }
}

@media only screen and (max-width: 768px) {
  .projectDetailsPage {
    margin-bottom: 60px;
    width: 100vw;
    .projectHero {
      align-items: center;
      justify-content: center;

      .projectTitle {
        align-items: center;
        flex-basis: 100%;
        span {
          -webkit-box-orient: vertical;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          overflow-y: hidden;
          text-align: center;
          text-overflow: ellipsis;
          word-break: break-word;
        }
      }
      .projectImg {
        display: none;
      }
    }

    .projectBody {
      .projectToc {
        display: none;
      }

      .projectContent {
        flex-basis: 100%;
        padding: 0 20px;
      }

      .projectDetailsContent {
        padding: 0;
      }
    }
  }
}
